.Spinner_loader__350wo,
.Spinner_loader__350wo:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Spinner_loader__350wo {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid #ffffff;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: Spinner_load8__VmWFc 1.1s infinite linear;
  animation: Spinner_load8__VmWFc 1.1s infinite linear;
}
@-webkit-keyframes Spinner_load8__VmWFc {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes Spinner_load8__VmWFc {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

